<template>
    <label class="ion-label label" :class="[modeClass,colorClass]"
           :fixed="fixed"
           :floating="floating"
           :stacked="stacked">
        <slot></slot>
    </label>
</template>
<style lang="scss">
    @import "./label.scss";
    @import "./label.ios.scss";
    @import "./label.md.scss";
</style>
<script type="text/javascript">
  /**
   * @component Label
   * @description
   *
   * ##  / Label
   *
   * ### 
   *
   * LabelItem, Input/Toggle/CheckboxFrom
   *
   * ### 
   *
   * input/Itemcontent.
   *
   * @props {String} mode - 
   * @props {String} color - 
   * @props {String} fixed - input
   * @props {String} floating - input, input
   * @props {String} stacked - input
   *
   * @slot  - 
   * @see component:Item
   * @see component:Input
   * */
  import { setElementClass } from '../../util/util'
  export default{
    name: 'Label',
    data () {
      return {
        itemComponent: null // Item
      }
    },
    props: {
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default () { return this.$config.get('mode') || 'ios' }
      },
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: [String],

      // label
      fixed: [Boolean],
      floating: [Boolean],
      stacked: [Boolean]
    },
    computed: {
      // 
      modeClass () {
        return `label-${this.mode}`
      },
      // 
      colorClass () {
        return this.color ? (`label-${this.mode}-${this.color}`) : ''
      }
    },
    mounted () {
      /**
       * fixed/floating/stackedinputlabel
       * fixed: label, input
       * floating:  input, labelinput; input, 
       * stacked: floating, , 
       * */
      if (this.$parent.$options._componentTag.toLowerCase() === 'item') {
        this.itemComponent = this.$parent
        setElementClass(this.itemComponent.$el, 'item-label-fixed', this.fixed)
        setElementClass(this.itemComponent.$el, 'item-label-floating', this.floating)
        setElementClass(this.itemComponent.$el, 'item-label-stacked', this.stacked)
        this.itemComponent.labelComponent = this
      }
    }
  }
</script>



